import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';

interface ServiceCardProps {
  id: number;
  title: string;
  description: string;
  category: string;
  price: number;
  deliveryTime: string;
  providerId: number;
  image: string;
}

export default function ServiceCard({
  id, 
  title, 
  description, 
  category, 
  price, 
  deliveryTime, 
  providerId,
  image
}: ServiceCardProps) {
  const [imageError, setImageError] = useState(false);
  
  return (
    <div className="bg-white rounded-lg overflow-hidden shadow-md hover:shadow-lg transition-shadow duration-300">
      <div className="relative h-48 w-full">
        <Image 
          src={imageError ? "data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 600 400'%3E%3Crect fill='%23cccccc' width='600' height='400'/%3E%3Cpath fill='%23ffffff' d='M300 160 L360 240 L240 240 Z'/%3E%3Cpath fill='%23ffffff' d='M360 240 L420 320 L180 320 L240 240 Z'/%3E%3Ccircle fill='%23ffffff' cx='460' cy='140' r='20'/%3E%3Ctext fill='%23888888' font-family='Arial' font-size='40' text-anchor='middle' x='300' y='180'%3E%3C/text%3E%3Ctext fill='%23888888' font-family='Arial' font-size='24' text-anchor='middle' x='300' y='350'%3E{category}%3C/text%3E%3C/svg%3E" : image} 
          alt={title}
          fill
          className="object-cover"
          onError={() => setImageError(true)}
        />
      </div>
      <div className="p-5">
        <div className="flex justify-between items-start mb-2">
          <h3 className="text-lg font-bold text-gray-900 mb-1">{title}</h3>
          <span className="bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded-full">{category}</span>
        </div>
        <p className="text-gray-600 text-sm line-clamp-2 mb-4">{description}</p>
        <div className="flex justify-between items-center">
          <div>
            <span className="text-2xl font-bold text-indigo-600">${price}</span>
            <span className="text-gray-500 text-sm"> / {deliveryTime}</span>
          </div>
          <Link 
            href={`/service-request?serviceId=${id}&providerId=${providerId}`}
            className="px-4 py-2 bg-indigo-600 hover:bg-indigo-700 text-white text-sm font-medium rounded-lg"
          >
            طلب الخدمة
          </Link>
        </div>
      </div>
    </div>
  );
} 