// تصنيفات الخدمات
const categories = [
  { id: 'all', name: 'جميع الخدمات' },
  { id: 'image-generation', name: 'توليد الصور' },
  { id: 'video-generation', name: 'توليد الفيديو' },
  { id: 'article-generation', name: 'كتابة المحتوى' },
  { id: 'static-story', name: 'القصص المصورة' },
  { id: 'research-generation', name: 'الأبحاث العلمية' },
  { id: 'website-app', name: 'المواقع والتطبيقات' },
  { id: 'model-training', name: 'تدريب النماذج' }
];

export default function ServicesGrid() {
  const [services, setServices] = useState<Service[]>([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  
  useEffect(() => {
    // محاكاة طلب API لجلب الخدمات
    const fetchServices = async () => {
      try {
        // في الإنتاج، سنقوم بطلب API حقيقي هنا
        // const response = await fetch('/api/services');
        // const data = await response.json();
        
        // حاليًا نستخدم بيانات افتراضية
        setTimeout(() => {
          setServices(defaultServices);
          setLoading(false);
        }, 500);
      } catch (error) {
        console.error('خطأ في جلب الخدمات:', error);
        setLoading(false);
      }
    };
    
    fetchServices();
  }, []);
  
  // تصفية الخدمات حسب التصنيف والبحث
  const filteredServices = services.filter((service) => {
    // تصفية حسب التصنيف
    if (activeCategory !== 'all' && service.category !== activeCategory) {
      return false;
    }
    
    // تصفية حسب البحث
    if (searchQuery && !service.title.includes(searchQuery) && !service.description.includes(searchQuery)) {
      return false;
    }
    
    return true;
  });
  
  return (
    <div className="py-8">
      <div className="container mx-auto px-4">
        <div className="mb-8 text-center">
          <h2 className="text-3xl font-bold text-gray-900 mb-2">خدماتنا</h2>
          <p className="text-lg text-gray-600">اختر من بين مجموعة واسعة من خدمات الذكاء الاصطناعي المتميزة</p>
        </div>
        
        {/* البحث والتصفية */}
        <div className="mb-8">
          <div className="flex flex-col md:flex-row gap-4 items-center justify-between">
            <div className="relative w-full md:w-96">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
              </div>
              <input
                type="text"
                className="block w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500"
                placeholder="ابحث عن خدمة..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            
            <div className="flex overflow-x-auto pb-2 gap-2 w-full md:w-auto">
              {categories.map((category) => (
                <button
                  key={category.id}
                  className={`px-4 py-2 rounded-full text-sm font-medium whitespace-nowrap ${
                    activeCategory === category.id
                      ? 'bg-indigo-600 text-white'
                      : 'bg-gray-100 text-gray-800 hover:bg-gray-200'
                  }`}
                  onClick={() => setActiveCategory(category.id)}
                >
                  {category.name}
                </button>
              ))}
            </div>
          </div>
        </div>
        
        {/* عرض الخدمات */}
        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
          </div>
        ) : filteredServices.length === 0 ? (
          <div className="text-center py-16">
            <p className="text-lg text-gray-600">لم يتم العثور على خدمات مطابقة للبحث</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {filteredServices.map((service) => (
              <ServiceCard
                key={service.id}
                id={service.id}
                title={service.title}
                description={service.description}
                category={service.category}
                price={service.price}
                deliveryTime={service.deliveryTime}
                providerId={service.providerId}
                image={service.image}
              />
            ))}
          </div>
        )}
      </div>
    </div>
  );
} 